/* reset */
button {
  all: unset;
}

#icon img {
  height: 50px;
  transition: height 0.2s ease;
}

.NavigationMenuRoot {
  position: sticky;
  top: 0;
  backdrop-filter: blur(5px);
  display: flex;
  /* right align */
  justify-content: right;
  width: 100vw;
  max-width: 100%;
  /* keep at top */
  z-index: 10;
}

.NavigationMenuList {
  display: flex;
  justify-content: center;
  background-color: transparent;
  padding: 8px;
  padding-top: 15px;
  padding-right: 20px;
  border-radius: 6px;
  list-style: none;
  /* box-shadow: 0 2px 10px var(--black); */
  margin: 0;
}

.NavigationMenuTrigger,
.NavigationMenuLink {
  padding: 8px 12px;
  outline: none;
  user-select: none;
  font-weight: 400;
  line-height: 1;
  border-radius: 4px;
  font-size: 15px;
  color: var(--light);

  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

#nav a:hover {
  text-decoration: none;
  color: var(--dark);
}

.NavigationMenuTrigger:focus,
.NavigationMenuLink:focus {
  /* box-shadow: 0 0 0 2px #000; */
}

.NavigationMenuTrigger:hover,
.NavigationMenuLink:hover {
  background-color: var(--light);
  color: var(--dark);
  opacity: 69%;

  text-shadow: none;
}

.NavigationMenuTrigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2px;
}

.NavigationMenuLink {
  display: block;
  text-decoration: none;
  font-size: 15px;
  line-height: 1;
}

.NavigationMenuContent {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  animation-duration: 250ms;
  animation-timing-function: ease;
}

.NavigationMenuContent[data-motion="from-start"] {
  animation-name: enterFromLeft;
}

.NavigationMenuContent[data-motion="from-end"] {
  animation-name: enterFromRight;
}

.NavigationMenuContent[data-motion="to-start"] {
  animation-name: exitToLeft;
}

.NavigationMenuContent[data-motion="to-end"] {
  animation-name: exitToRight;
}

@media only screen and (min-width: 600px) {
  .NavigationMenuContent {
    width: auto;
  }
}

.NavigationMenuIndicator {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 10px;
  top: 100%;
  overflow: hidden;
  z-index: 1;
  transition: width, transform 250ms ease;
}

.NavigationMenuIndicator[data-state="visible"] {
  animation: fadeIn 200ms ease;
}

.NavigationMenuIndicator[data-state="hidden"] {
  animation: fadeOut 200ms ease;
}

.NavigationMenuViewport {
  position: relative;
  transform-origin: top center;
  margin-top: 10px;
  margin-right: 1%;
  width: 100%;
  background-color: var(--light2);
  border-radius: 6px;
  overflow: hidden;
  /* box-shadow: hsl(206 22% 7% / 35%) 0px 10px 38px -10px, hsl(206 22% 7% / 20%) 0px 10px 20px -15px; */
  /* height: 250px; */
  /* HEIGHT OF BOX */
  transition: width, height, 300ms ease;
}

.NavigationMenuViewport[data-state="open"] {
  animation: scaleIn 200ms ease;
}

.NavigationMenuViewport[data-state="closed"] {
  animation: scaleOut 200ms ease;
}

@media only screen and (min-width: 600px) {
  .NavigationMenuViewport {
    width: var(--radix-navigation-menu-viewport-width);
    height: var(--radix-navigation-menu-viewport-height);
  }
}

@media only screen and (max-width: 600px) {
  .NavigationMenuViewport {
    width: 100%;
    height: var(--radix-navigation-menu-viewport-height);
  }
}

.List {
  display: grid;
  padding: 22px;
  margin: 0;
  column-gap: 10px;
  list-style: none;
}

@media only screen and (min-width: 600px) {
  .List.one {
    width: 500px;
    grid-template-columns: 0.75fr 1fr;
  }

  .List.two {
    width: 300px;
    grid-auto-flow: column;
    grid-template-rows: repeat(2, 1fr);
  }
}

.ListItemLink {
  display: block;
  outline: none;
  text-decoration: none;
  user-select: none;
  padding: 12px;
  border-radius: 6px;
  font-size: 15px;
  line-height: 1;
}

.ListItemLink:focus {
  /* box-shadow: 0 0 0 2px #FFF; */
}

.ListItemLink:hover {
  background-color: var(--light);

  .ListItemHeading,
  .ListItemText {
    color: var(--black);
  }
}

.ListItemHeading {
  font-weight: 400;
  line-height: 1.2;
  margin-bottom: 5px;
  color: var(--white);
}

.ListItemText {
  color: var(--white);
  font-size: 15px;
  line-height: 1.4;
  font-weight: lighter;
}

.Callout {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--interact) 0%, var(--dark) 100%);
  border-radius: 6px;
  /* padding: 5%; */
  padding-left: 20px;
  padding-top: 15px;
  text-decoration: none !important;
  outline: none;
  user-select: none;
}

.Callout:focus {
  /* box-shadow: 0 0 0 2px var(--violet-7); */
}

.CalloutHeading {
  color: var(--white);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2;
  margin-top: 10%;
  margin-bottom: 5%;
}

.CalloutText {
  color: var(--white);
  font-size: 14px;
  line-height: 1.3;
}

.ViewportPosition {
  position: absolute;
  display: flex;
  justify-content: right;
  width: 100%;
  top: 100%;
  left: 0;
  perspective: 2000px;
}

.CaretDown {
  position: relative;
  color: var(--light);
  top: 1px;
  transition: transform 250ms ease;
}

[data-state="open"] > .CaretDown {
  transform: rotate(-180deg);
  color: var(--dark);
}

.Arrow {
  position: relative;
  top: 70%;
  background-color: var(--light2);
  width: 10px;
  height: 10px;
  transform: rotate(45deg);
  border-top-left-radius: 2px;
}

@keyframes enterFromRight {
  from {
    opacity: 0;
    transform: translateX(200px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes enterFromLeft {
  from {
    opacity: 0;
    transform: translateX(-200px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes exitToRight {
  from {
    opacity: 1;
    transform: translateX(0);
  }

  to {
    opacity: 0;
    transform: translateX(200px);
  }
}

@keyframes exitToLeft {
  from {
    opacity: 1;
    transform: translateX(0);
  }

  to {
    opacity: 0;
    transform: translateX(-200px);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: rotateX(-30deg) scale(0.9);
  }

  to {
    opacity: 1;
    transform: rotateX(0deg) scale(1);
  }
}

@keyframes scaleOut {
  from {
    opacity: 1;
    transform: rotateX(0deg) scale(1);
  }

  to {
    opacity: 0;
    transform: rotateX(-10deg) scale(0.95);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.SwitchRoot {
  width: 42px;
  height: 25px;
  background-color: var(--light);
  border-radius: 9999px;
  position: relative;
  /* box-shadow: 0 2px 10px var(--black); */
  margin-left: 10px;
  margin-top: 2px;
}

.SwitchThumb {
  display: block;
  width: 21px;
  height: 21px;
  background-color: var(--dark);
  border-radius: 9999px;
  /* box-shadow: 0 2px 2px var(--black); */
  transition: transform 100ms;
  transform: translateX(2px);
  will-change: transform;
}

.SwitchThumb[data-state="checked"] {
  transform: translateX(19px);
}

/* Mobile optimizations */
@media only screen and (max-width: 600px) {
  #icon {
    padding: 4px !important;
    padding-top: 8px !important;
  }

  #icon img {
    height: 35px;
  }

  .NavigationMenuList {
    padding: 4px;
    padding-top: 12px;
    padding-right: 12px;
  }

  .NavigationMenuTrigger,
  .NavigationMenuLink {
    padding: 6px 8px;
    font-size: 14px;
  }

  #nav {
    display: flex;
    align-items: center;
    margin-top: -2px;
  }
}
